<template>
  <div id="app">
    <el-container style="height: 500px; border: 1px solid #eee">
      <el-header class="d-flex justify-content-between">
        <span class="font-weight-bold"></span>
        <div style="text-align:right">
          <el-dropdown>
            <i class="el-icon-setting" style="margin-right: 15px"></i>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item><span @click="quit"></span></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <span>{{this.$store.state.admin.aname}}&nbsp;</span>
        </div>
      </el-header>
      <el-container>
        <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <el-menu :default-openeds="['1']">
          <el-submenu index="1">
            <template slot="title"><i class="el-icon-message"></i></template>
            <el-menu-item-group>
              <el-menu-item index="1-1" @click="adminu"></el-menu-item>
              <el-menu-item index="1-2" @click="adminp"></el-menu-item>
              <el-menu-item index="1-3" @click="admind"></el-menu-item>
              <el-menu-item index="1-4" class="is-active"></el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>
        <el-main>
          <el-collapse v-model="activeName" accordion>
            <el-collapse-item title="---" name="1">
              <div class="bar" ref="bar_ref"></div>
            </el-collapse-item>
            <el-collapse-item title="---" name="2">
              <div class="pie" ref="pie_ref"></div>
            </el-collapse-item>
            <el-collapse-item title="" name="3">
              <div>
                <div class="h1"></div>
                <div class="font-weight-bold"></div>
              </div>
            </el-collapse-item>
            <el-collapse-item title="" name="4">
              <div>
                <div class="h1"></div>
                <div class="font-weight-bold"></div>
              </div>
            </el-collapse-item>
          </el-collapse>
        </el-main>
      </el-container>
    </el-container>
    <el-dialog
      title=""
      :visible.sync="quitVisible">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="quitVisible = false"> </el-button>
        <el-button type="primary" @click="close"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      //
      aname:'',
      //
      quitVisible:false,
      //
      activeName: '1',
      //
      categoryName:[],
      //
      result:[],
      //
      barInit:null
    }
  },
  mounted(){
    //
    this.aname=this.$route.params.aname
    this.barChart()
    this.getCategoryName()
    this.pieChart()
  },
  methods:{
    //
    async getCategoryName(){
      this.$store.state.category=[]
      await this.axios.get(this.host+"/product/p1/getcategory").then(res => {
        this.result=res.data
        this.result.forEach(item => {
          this.categoryName.push(item.cname)
          this.axios.get(this.host+"/product/p1/count",{
            params:{
              categoryid:item.cid
            }
          }).then(res => {
            this.$store.commit('addCategory',res.data[0].count)
          })
        })
        // 
        let changeoption = {
          xAxis: {
            data: this.categoryName
          },
        }
        this.barInit.setOption(changeoption)
      })
    },
    barChart(){
      this.barInit = echarts.init(this.$refs.bar_ref);
      let option = {
         title: {
            text: '',
            textStyle:{
              color:'#2e7df6'
            },
            left:'center'
          },
          tooltip: {},
          xAxis:{
            data:["","","","","",""],
            axisLine:{
              lineStyle:{
                color:'#2e7df6'
              }
            }
          },
          yAxis: {
            axisLine:{
              lineStyle:{
                color:'#2e7df6'
              }
            },
            axisLabel:{
              textStyle: {
                color:'#2e7df6'
              }
            }
          },
          series: [{
            color:{
              type: 'linear',
              x: 0,
              y: 0,
              x2: 1,
              y2: 1,
              colorStops: [{
                  offset: 0, color: '#2e7df6' // 0% 
              }, {
                  offset: 1, color: '#5052EE' // 100% 
              }],
            },
            name: '',
            type: 'bar',
            data: this.$store.state.category,
            label:{
              show: true,
              position: 'top',
              textStyle: {
                color: '#47a1f7'
              }
            }
          }]
        }
        this.barInit.setOption(option)
    },
    async pieChart(){
      this.$store.state.dscore=[
        {value: 0, name: '0'},
        {value: 0, name: '1'},
        {value: 0, name: '2'},
        {value: 0, name: '3'},
        {value: 0, name: '4'},
        {value: 0, name: '5'}
      ]
      await this.axios.get(this.host+"/discuss/d1/getdscore").then(res => {
        let result=res.data
        result.forEach(item => {
          this.$store.commit('addDscore',item.dscore)
        })
      })
      let pieInit = echarts.init(this.$refs.pie_ref);
      let option = {
        title: {
          text: '',
          subtext: '',
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'vertical',
          left: 'left',
        },
        series: [{
          name: '',
          type: 'pie',
          radius: '50%',
          data: this.$store.state.dscore,
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }]
      }
      pieInit.setOption(option)
    },
    quit(){
      this.quitVisible=true
    },
    close(){
      this.$router.push('/alog')
    },
    adminp(){
      this.$router.push('/ap')
    },
    adminu(){
      this.$router.push('/au')
    },
    admind(){
      this.$router.push('/ad')
    }
  }
}
</script>
<style scoped>
.el-header {
    background: linear-gradient(to right,#EEF1F6,#EEF1FD,#EEF1F6);
    color: #333;
    line-height: 60px;
}
.el-aside {
  color: #333;
}
.bar{
  width: 640px;
  height: 330px;
}
.pie{
  width: 640px;
  height: 330px;
}
</style>